<template>
  <div class="practice">
    <el-card shadow="never">
      <el-form>
        <el-form-item>
          <el-row type="flex" justify="space-between">
            <el-col :span="6">
            </el-col>
            <el-col :span="18" class="text_right">
              <el-button type="primary" size="small" @click="addData">创建题库</el-button>
              <el-button type="primary" size="small" @click="showUpload">题库导入</el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>

      <div class="table" v-loading="searchLoading">
        <common-table :columns="dataColumns" :datas="dataList" :pagger="false" th-class="table-innerhead" border>
          <template slot="CreateTime" slot-scope="scope">
            <span>{{formatDate(scope.row.CreateTime, 'yyyy-MM-dd hh:mm:ss')}}</span>
          </template>
          <template slot="oprate" slot-scope="scope">
            <el-button type="success" size="mini" @click="editData(scope.row)">修改</el-button>
            <el-button type="warning" size="mini" @click="previewData(scope.row)">预览</el-button>
            <el-button type="danger" size="mini" @click="deleteData(scope.row)">移除</el-button>
          </template>
        </common-table>
      </div>
    </el-card>

    <el-dialog :title="dataEdit?'题库修改':'题库创建'" :visible.sync="showEdit" width="800px" center @close="closeDialog">
      <!-- 编辑标题 -->
      <div v-show="editTitle">
        <el-form ref="formTitle" :model="formTitle" :rules="rulesTitle" label-width="80px">
          <el-form-item label="题库名称" prop="title">
            <el-input v-model="formTitle.title" placeholder="请输入题库名称"></el-input>
          </el-form-item>
          <el-form-item style="text-align:center;">
            <div v-if="dataEdit">
              <el-button size="small" @click="showEdit = false">取消</el-button>
              <el-button
                size="small"
                :loading="btnLoading"
                type="primary"
                @click="saveFormTitle('formTitle')"
              >提交修改
              </el-button>
            </div>
            <div v-else>
              <el-button size="small" @click="showEdit = false">取 消</el-button>
              <el-button
                size="small"
                :loading="btnLoading"
                type="primary"
                @click="saveFormTitle('formTitle')"
              >下一步
              </el-button>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <!-- 编辑题目 -->
      <div v-show="!editTitle">
        <div class="dialog_title">编辑第{{ (subjectIndex + 1) }}题</div>
        <el-form ref="formSubject" :model="formSubject" :rules="rulesSubject" label-width="80px">
          <el-form-item label="题目类型" prop="type">
            <el-select
              v-model="formSubject.type"
              placeholder="请选择题目类型"
              style="width:100%;"
              @change="subjectTypeChange"
            >
              <el-option
                v-for="(item, index) in subjectTypeOptions"
                :key="index"
                :label="item.label"
                v-model="item.value"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="题目标题" prop="title">
            <el-input
              type="textarea"
              v-model="formSubject.title"
              :autosize="{ minRows: 1, maxRows: 3}"
              placeholder="请输入题目标题"
              maxlength="1000"
              show-word-limit
            ></el-input>
          </el-form-item>

          <el-form-item v-if="[1, 2].indexOf(formSubject.type) > -1" label="题目选项" required>
            <div style="text-align: right; margin-right: 15px;">
              <el-button
                v-show="formSubject.options.length<8"
                type="primary"
                size="small"
                @click="subjectOptionsAdd"
              >新增
              </el-button>
            </div>

            <el-table size="mini" :data="formSubject.options" :cell-style="{border:0}"
                      style="width: 100%; margin-bottom:0;">
              <el-table-column prop="value" label="选项" width="60"></el-table-column>
              <el-table-column prop="label" label="选项内容">
                <template slot-scope="scope">
                  <el-form-item
                    :prop="'options.' + scope.$index + '.label'"
                    :rules="rulesSubject.options.label" style="margin-bottom: 15px;"
                  >
                    <el-input v-model="scope.row.label" placeholder="请输入选项内容"></el-input>
                  </el-form-item>
                </template>
              </el-table-column>
              <el-table-column label="操作" width="80">
                <template slot-scope="scope">
                  <el-button
                    v-show="formSubject.options.length>2"
                    type="danger"
                    size="small"
                    @click="subjectOptionsDelete(scope.row)"
                  >删除
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-form-item>

          <el-form-item v-if="formSubject.type===2" label="题目答案" prop="multiAnswer">
            <el-checkbox-group v-model="formSubject.multiAnswer">
              <el-checkbox-button
                v-for="(item, index) in formSubject.options"
                :key="index"
                :label="item.value"
                :value="item.value"
              ></el-checkbox-button>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item v-else label="题目答案" prop="answer"
                        :rules="{required: formSubject.type!==4, message: '请选择或输入题目答案', trigger: 'change'}">
            <el-radio-group v-if="formSubject.type===1" v-model="formSubject.answer">
              <el-radio-button
                v-for="(item, index) in formSubject.options"
                :key="index"
                :label="item.value"
                :value="item.value"
              ></el-radio-button>
            </el-radio-group>

            <el-radio-group v-else-if="formSubject.type===3" v-model="formSubject.answer">
              <el-radio-button
                v-for="(item, index) in judgeOptions"
                :key="index"
                :label="item"
                :value="item"
              ></el-radio-button>
            </el-radio-group>

            <el-input
              v-else
              type="textarea"
              v-model="formSubject.answer"
              :autosize="{ minRows: 2, maxRows: 5}"
              placeholder="请输入题目答案"
              maxlength="1000"
              show-word-limit
            ></el-input>
          </el-form-item>

          <el-form-item label="题目分数" prop="score">
            <el-input-number v-model="formSubject.score" :min="1" :max="1000" label="请输入题目分数" controls-position="right"
                             :precision="0" :step="1"></el-input-number>
          </el-form-item>

          <el-form-item label="题目解析" prop="analysis">
            <el-input
              type="textarea"
              v-model="formSubject.analysis"
              :autosize="{ minRows: 2, maxRows: 5}"
              placeholder="题目解析"
              maxlength="1000"
              show-word-limit
            ></el-input>
          </el-form-item>

          <el-form-item style="text-align:center;">
            <div v-if="dataEdit">
              <el-button size="small" @click="showEdit = false">取消</el-button>
              <el-button
                size="small"
                :loading="btnLoading"
                type="primary"
                @click="saveFormSubject('formSubject')"
              >
                <span v-if="formSubject.isAdd">提交保存</span>
                <span v-else>提交修改</span>
              </el-button>
            </div>
            <div v-else>
              <el-button size="small" @click="showEdit = false">关闭</el-button>
              <el-button
                size="small"
                :loading="btnLoading"
                :disabled="btnDisabled"
                type="primary"
                @click="saveFormSubject('formSubject')"
              >保存
              </el-button>
              <el-button size="small" v-if="btnDisabled" type="primary" @click="nextSubject()">下一题</el-button>
            </div>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>

    <el-dialog :title="dataEdit?'随堂练习修改':'随堂练习预览'" :visible.sync="showPreview" width="800px" center
               @close="closePreviewDialog">
      <div class="pre_title">
        <span>题库名称：</span>
        <span>{{dataInfo.title}}</span>
        <div v-show="dataEdit" class="title_btn">
          <el-button type="success" size="mini" @click="toTitleEdit()">修改</el-button>
          <el-button type="primary" size="mini" @click="toSubjectAdd()">新增题目</el-button>
        </div>
      </div>
      <div class="pre_line">
        <!--        <span>试卷计时：</span>-->
        <!--        <span>{{dataInfo.time}}</span>-->
        <span>总题数：</span>
        <span>{{dataInfo.subjectList.length}}</span>
        <span>总分数：</span>
        <span>{{dataInfo.score}}</span>
      </div>

      <el-collapse v-model="dataInfo.subjectActive" class="collapse_main" accordion>
        <el-collapse-item v-for="(item, index) in dataInfo.subjectList" :key="index" :name="index">
          <template slot="title">
            <el-row :gutter="20" class="collapse_head">
              <el-col :span="dataEdit ? 18 : 24">
                <span>{{ (dataEdit?(dataInfo.subjectList.length-index):(index+1) )+'、['+ subjectTypeOptions[item.type-1].label +']'+ item.title}}</span>
              </el-col>
              <el-col v-if="dataEdit" :span="6">
                <el-button type="success" size="mini" @click.stop="toSubjectEdit(item, index)">修 改</el-button>
                <el-button type="danger" size="mini" @click.stop="toSubjectDelete(item, index)">删 除</el-button>
              </el-col>
            </el-row>
          </template>
          <el-card class="collapse_card">
            <div v-if="[1, 2].indexOf(item.type) > -1">
              <h4>选项：</h4>
              <p v-for="(sub, k) in item.options" :key="k">{{sub.value +'、'+sub.label}}</p>
            </div>
            <h4>答案：</h4>
            <div class="card_line">
              <span>正确答案：</span>
              <span>{{item.type===2?item.multiAnswer.join(''):item.answer}}</span>
              <span>分数：</span>
              <span>{{item.score}}</span>
            </div>
            <h4>解析：</h4>
            <div class="card_content">{{item.analysis}}</div>
          </el-card>
        </el-collapse-item>
      </el-collapse>
      <div style="text-align:right;margin-top:15px;">
        <el-button size="small" @click="showPreview=false">关 闭</el-button>
      </div>
    </el-dialog>

    <el-dialog
      title="题库导入"
      class="modal-xlg"
      :close-on-click-modal="false"
      :visible.sync="isShowUploadForm"
      width="500px" @close="resetform">
      <div class="fileupload-buttonbar">
        <el-upload
          class="upload-demo"
          ref="upload"
          action
          :on-preview="handlePreview"
          :before-upload="beforeUpload"
          :auto-upload="false"
          :on-change="uploadchange"
          :on-remove="handleRemove"
          :limit="1"
          :on-exceed="uploadExceed">
          <el-button type="success" icon="el-icon-download" style="margin:0 0px 0 10px;" size="small" :disabled="isDisabled" @click="downloadFile">下载模板</el-button>
          <el-button type="primary" icon="el-icon-plus" slot="trigger" size="small" :disabled="isDisabled">选取文件</el-button>
          <el-button type="success" icon="el-icon-upload" size="small" @click="submitUpload" :disabled="isDisabled">导入</el-button>
          <div slot="tip" class="el-upload__tip">只能上传Word(docx/doc)文件，文件最大100M</div>
        </el-upload>
        <div class="import-info" v-if="isDisabled">正在导入，请耐心等待...</div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import list from "@/mixin/list";
  import { formatDate } from "@/utils/utils";

  export default {
    mixins: [list],
    data() {
      return {
        showEdit: false,
        editTitle: true, //默认编辑标题
        showPreview: false,
        searchLoading: false,
        dataColumns: [
          {
            prop: "PLTitle",
            label: "题库名称"
          },
          // {
          //   prop: "PLType",
          //   label: "题库类型"
          // },
          {
            prop: "CreateTime",
            label: "创建时间",
            slot: true
          },
          {
            prop: "oprate",
            label: "操作",
            slot: true,
            width: 220
          }
        ],
        dataList: [],

        formTitle: {
          id: "",
          title: "",
          time: 0
        },
        rulesTitle: {
          title: {
            required: true,
            min: 1,
            max: 80,
            message: "长度在 1 到 80 个字符",
            trigger: "blur"
          }
        },
        subjectIndex: 0,
        subjectTypeOptions: [
          { label: "单选题", value: 1 },
          { label: "多选题", value: 2 },
          { label: "判断题", value: 3 },
          { label: "问答题", value: 4 }
        ],
        singleOptions: ["A", "B", "C", "D", "E", "F", "G", "H"],
        // multiOptions: ["A", "B", "C", "D"],
        judgeOptions: ["对", "错"],
        formSubject: {
          isAdd: true,
          fromId: "",
          type: 1,
          title: "",
          options: [],
          answer: "",
          multiAnswer: [],
          score: 0,
          analysis: ""
        },

        rulesSubject: {
          title: {
            required: true,
            min: 1,
            max: 500,
            message: "长度在 1 到 500 个字符",
            trigger: "blur"
          },
          options: {
            label: {
              required: true,
              min: 1,
              max: 50,
              message: "长度在 1 到 50 个字符",
              trigger: "blur"
            },
            value: {
              required: true,
              min: 1,
              max: 50,
              message: "长度在 1 到 50 个字符",
              trigger: "blur"
            }
          },
          // answer: { required: true, message: "请选择或输入题目答案", trigger: "change" },
          multiAnswer: {
            required: true,
            type: "array",
            message: "请至少选择一个答案",
            trigger: "change"
          },
          score: [
            { required: true, message: "题目分数不能为空" },
            { type: "number", message: "题目分数必须为数字值" }
          ],
          analysis: {
            required: false,
            min: 1,
            max: 500,
            message: "长度在 1 到 500 个字符",
            trigger: "blur"
          },
          time: [
            { required: true, message: "试卷计时不能为空" },
            { type: "number", message: "试卷计时必须为数字值" }
          ]
        },

        // practiceRules: `试卷模板规则：
        // 1.每道题目……
        // 2.如果有答案前面加解析`,
        btnLoading: false,
        btnDisabled: false,
        dialogChange: false, //对话框是否有更新事件，此参数用来更新查询列表
        dataEdit: false, //是否修改试卷
        dataEditRow: {}, //编辑的随堂练习
        dataInfo: {
          id: "",
          title: "",
          time: 0,
          score: 0,
          subjectActive: 0,
          subjectList: []
        },

        isShowUploadForm: false,
        //文件上传列表默认是无
        myFileList: 0,
        isDisabled: false
      };
    },
    created() {
      this.searchData();
    },
    methods: {
      formatDate(dateValue, format) {
        return formatDate(dateValue, format);
      },
      closeDialog() {
        if (this.dialogChange) this.searchData();
      },
      searchData() {
        this.searchLoading = true;
        this.dataList = [];
        this.$http
          .get(this.$api.PracticeBankList, { token: this.$http.getToken() })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push(item);
              });
            }
            if (res.Result) {
              this.dataTotal = res.RowsCount || 0;
            }
            this.searchLoading = false;
          })
          .catch(err => {
            this.searchLoading = false;
          });
      },
      editData(row) {
        this.dataEdit = true;
        this.dataEditRow = row;
        this.getData(row);
        this.showPreview = true;
      },
      previewData(row) {
        this.getData(row);
        this.showPreview = true;
      },

      /**
       * 保存标题
       */
      saveFormTitle(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let reqData = {
              token: this.$http.getToken(),
              plID: this.formTitle.id,
              plType: "",
              plTitle: this.formTitle.title,
            };
            this.$http
              .post(this.$api.PracticeBankSave, reqData)
              .then(res => {
                if (res.Result) {
                  this.editTitle = false;
                  this.dialogChange = true;
                  if (this.dataEdit) {
                    this.dataInfo.title = this.formTitle.title;
                    this.dataInfo.time = this.formTitle.time;
                    this.showEdit = false;
                    this.$message({
                      message: "修改成功！",
                      type: "success"
                    });
                  } else {
                    this.resetFormSubject();
                    this.formSubject.fromId = "";
                    if (res.Data) this.formSubject.fromId = res.Data;
                  }
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },
      /**
       * 保存题目(单题编辑)
       */
      saveFormSubject(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let subjectMain = {
              PLID: this.formSubject.fromId,
              PSType: this.formSubject.type,
              PSContent: this.formSubject.title,
              PSAnswer:
                this.formSubject.type === 2
                  ? this.formSubject.multiAnswer.join("")
                  : this.formSubject.answer,
              PSanalysis: this.formSubject.analysis,
              PSScore: this.formSubject.score
            };
            if (this.showEdit) subjectMain.PSID = this.formSubject.id;
            let subjectOption = {
              Datas:
                this.formSubject.type === 4
                  ? []
                  : this.formSubject.options.map(r => {
                    return { SOSerial: r.value, SOContent: r.label };
                  })
            };

            let reqData = {
              psdata: JSON.stringify(subjectMain),
              sodata: JSON.stringify(subjectOption)
            };
            let reqUrl =
              this.dataEdit && !this.formSubject.isAdd
                ? this.$api.ClassPractiseSubjectUpdate
                : this.$api.ClassPractiseSubjectAdd;
            this.$http
              .post(reqUrl, reqData)
              .then(res => {
                if (res.Result) {
                  // this.editTitle = false;
                  if (this.dataEdit) {
                    this.showEdit = false;
                    if (this.formSubject.isAdd) {
                      // this.dataInfo.subjectList.splice(0, 0, this.formSubject);
                      // this.dataInfo.score += this.formSubject.score; // 更新分数
                      this.getData(this.dataEditRow);
                    } else {
                      // 先更新分数
                      this.dataInfo.score +=
                        this.formSubject.score -
                        this.dataInfo.subjectList[this.subjectIndex].score;

                      this.dataInfo.subjectList.splice(
                        this.subjectIndex,
                        1,
                        this.formSubject
                      );
                    }

                    this.$message({
                      message: "操作成功！",
                      type: "success"
                    });
                  } else {
                    this.btnDisabled = true;
                  }
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },
      nextSubject() {
        this.subjectIndex++;
        // console.log(" this.subjectIndex:", this.subjectIndex);
        this.resetFormSubject();
      },
      resetFormSubject() {
        this.btnDisabled = false;
        // this.subjectIndex = 0;

        if (this.$refs["formSubject"]) this.$refs["formSubject"].resetFields();
        this.formSubject.isAdd = true;
        this.formSubject.id = "";
        this.formSubject.type = 1;

        // this.formSubject.id = "";
        // this.formSubject.type = 1;
        this.formSubject.title = "";
        // this.formSubject.options = [];
        this.formSubject.answer = "";
        // this.formSubject.multiAnswer = [];
        this.formSubject.score = 0;
        this.formSubject.analysis = "";

        this.subjectTypeChange(1);
      },
      // 题型选择
      subjectTypeChange(val) {
        if (val === 1 || val === 2) {
          this.formSubject.options = [];
          for (let i = 0; i < 4; i++) {
            this.formSubject.options.push({
              label: "",
              value: this.singleOptions[i]
            });
          }
        }

        this.formSubject.answer = "";
        this.formSubject.multiAnswer = [];
      },
      // 新增选项
      subjectOptionsAdd() {
        if (this.formSubject.type === 1 || this.formSubject.type === 2) {
          this.formSubject.options.push({
            label: "",
            value: this.singleOptions[this.formSubject.options.length]
          });
        }
      },
      // 删除选项
      subjectOptionsDelete(row) {
        if (this.formSubject.options.length < 3) {
          this.$message({
            showClose: true,
            message: "必须有两个以上选项",
            type: "error"
          });
          return;
        }

        let index = this.formSubject.options.indexOf(row);
        if (index > -1) this.formSubject.options.splice(index, 1);
        this.formSubject.options.map((item, i) => {
          item.value = this.singleOptions[i];
        });
      },
      deleteData(row) {
        this.$confirm("此操作将删除题库, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$http
              .post(this.$api.PracticeBankDelete, {
                ids: row["PLID"],
                state: -1
              })
              .then(res => {
                if (!res.Result) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                } else {
                  this.$message({
                    showClose: true,
                    message: "删除成功",
                    type: "success"
                  });
                  this.searchData();
                }
              })
              .catch(err => {
                this.$message({
                  showClose: true,
                  message: "操作失败",
                  type: "error"
                });
              });
          })
          .catch(err => {
          });
      },
      getData(row) {
        this.dataInfo.subjectList = [];
        this.dataInfo.id = row["PLID"] + "";
        this.dataInfo.title = row["PLTitle"] + "";
        // this.dataInfo.time = row["CPTime"] || 0;
        this.dataInfo.score = 0;

        this.$http
          .get(this.$api.ClassPractiseSubjectList, { plid: row["PLID"] })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataInfo.subjectList.push({
                  fromId: item.Key["PLID"],
                  id: item.Key["PSID"],
                  no: item.Key["PSSerial"] || 0,
                  title: item.Key["PSContent"],
                  type: item.Key["PSType"],
                  score: item.Key["PSScore"] || 0,
                  analysis: item.Key["PSanalysis"],
                  answer: item.Key["PSAnswer"],
                  multiAnswer:
                    item.Key["PSType"] !== 2
                      ? []
                      : item.Key["PSAnswer"].split(""),
                  options:
                    item.Key["PSType"] === 4
                      ? []
                      : item.Value.map(v => {
                        return { label: v["SOContent"], value: v["SOSerial"] };
                      })
                });
                this.dataInfo.score += item.Key["PSScore"] || 0;
              });
              if (this.dataEdit) this.dataInfo.subjectList.reverse();
            }
            // if (res.Result) {
            //   this.dataTotal = res.RowsCount || 0;
            // }
            // this.searchLoading = false;
          })
          .catch(err => {
            // this.searchLoading = false;
          });
      },
      addData() {
        this.resetFormTitle();
        this.editTitle = true;
        this.showEdit = true;
      },
      resetFormTitle() {
        this.formTitle = {
          id: "",
          title: "",
          time: 0
        };
      },

      // 关闭编辑和预览窗体
      closePreviewDialog() {
        this.dataEdit = false;
      },
      // 修改试卷主体信息
      toTitleEdit() {
        this.showEdit = true;
        this.editTitle = true;
        this.formTitle.id = this.dataInfo.id;
        this.formTitle.title = this.dataInfo.title;
        this.formTitle.time = this.dataInfo.time;
      },
      // 新增试卷特定题目
      toSubjectAdd() {
        this.showEdit = true;
        this.editTitle = false;

        this.subjectIndex = this.dataInfo.subjectList.length;

        if (this.$refs["formSubject"]) this.$refs["formSubject"].resetFields();
        this.formSubject.fromId = this.dataInfo.id;
        this.formSubject.isAdd = true;
        this.formSubject.type = 1;

        this.formSubject.title = "";
        this.formSubject.answer = "";
        this.formSubject.score = 0;
        this.formSubject.analysis = "";

        //设置默认选项
        this.subjectTypeChange(1);
      },
      // 修改试卷特定题目 数据渲染
      toSubjectEdit(item, index) {
        if (this.btnLoading) return;
        this.showEdit = true;
        this.editTitle = false;

        this.subjectIndex = index;

        this.formSubject.isAdd = false;
        this.formSubject.type = item.type;
        this.subjectTypeChange(item.type); //必须在选项和答案赋值之前处理
        this.formSubject.fromId = item.fromId;
        this.formSubject.id = item.id;
        this.formSubject.title = item.title;
        this.formSubject.options = item.options;
        this.formSubject.answer = item.answer;
        this.formSubject.multiAnswer = item.multiAnswer;
        this.formSubject.score = item.score;
        this.formSubject.analysis = item.analysis;
      },
      // 删除试卷特定题目
      toSubjectDelete(item, index) {
        if (this.btnLoading) return;
        this.$confirm("此操作将删除试卷题目, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.btnLoading = true;
            this.$http
              .post(this.$api.ClassPractiseSubjectDelete, {
                plid: item.fromId,
                psid: item.id
              })
              .then(res => {
                if (!res.Result) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                } else {
                  this.$message({
                    showClose: true,
                    message: "删除成功",
                    type: "success"
                  });
                  // 先更新分数
                  this.dataInfo.score -= this.dataInfo.subjectList[index].score;
                  this.dataInfo.subjectList.splice(index, 1);
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.$message({
                  showClose: true,
                  message: "操作失败",
                  type: "error"
                });
                this.btnLoading = false;
              });
          })
          .catch(err => {
          });
      },

      /**
       * 题库导入
       */
      showUpload() {
        this.isShowUploadForm = true;
      },

      async downloadFile() {
        let downloadURL = await this.$api.GetDownloadFileURL();
        window.location.href =
          downloadURL +
          "?fileName=题库模板" +
          "&fileType=13";
      },

      //上传文件
      resetform() {
        this.$refs.upload.clearFiles();
      },
      handleUpload() {
        this.isShowUploadForm = true;
      },
      uploadExceed(files, filelist) {
        this.$message.error("上传文件超出个数");
      },
      //文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
      uploadchange(file, filelist) {
        this.myFileList = filelist.length;
      },
      handleRemove(file, filelist) {
        this.$refs.upload.clearFiles();
      },
      
      submitUpload() {
        if (this.myFileList === 0) {
          this.$message.error("请先上传文件");
          return;
        }

        this.isDisabled = true;
        this.$refs.upload.submit();
      },
      handlePreview(file) {
      
      },

      beforeUpload: async function(file) {   
        //这里是重点，将文件转化为formdata数据上传
        // console.log(file, "文件上传前");
        let fd = new FormData();
        fd.append("stream", file);
        fd.append("fileType", 13);
        const res = await this.$http.post(this.$api.UploadFile, fd);
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          const res1 = await this.$http.get(this.$api.ImportPractice, { uid: this.$http.getUser().UserID, filename: res.Data });
          if (res1.Result) {
            this.$message({
              message: "导入成功",
              type: "success"
            });

            this.searchData();
          } else {
            this.$message({
              showClose: true,
              message: "数据有误：" + res1.message,
              type: "error"
            });
          }
        }

        this.isShowUploadForm = false;
        this.isDisabled = false;
        return false;
      },
    }
  }
</script>
<style lang="less" scoped>
  .practice {
    text-align: left;
  }

  .modal-xlg {
    text-align: center;
  }

  .text_right {
    text-align: right;
  }

  .dialog_title {
    font-size: 16px;
    text-align: center;
    margin-bottom: 15px;
  }

  .pre_title {
    font-size: 16px;
    line-height: 2;

    .title_btn {
      display: inline-block;
      padding-left: 30px;
    }
  }

  .pre_line {
    font-size: 14px;
    line-height: 2;

    span:nth-child(2n) {
      padding-right: 30px;
    }
  }

  /deep/ .el-collapse-item__header {
    min-height: 48px;
    height: unset;
    line-height: 1.2;
  }

  .collapse_main {
    margin-top: 20px;
    word-break: break-all;
    word-wrap: break-word;

    .collapse_head {
      width: 100%;

      span {
        font-size: 15px;
        line-height: 1.5;
      }
    }

    .collapse_card {
      font-size: 14px;

      h4 {
        font-size: 16px;
        border-left: 5px solid #59d8ff;
        padding-left: 10px;
      }

      p {
        font-size: 14px;
        line-height: 1.5;
        margin-bottom: 0;
        margin-left: 20px;
      }

      .card_line {
        margin-left: 20px;
        line-height: 2;

        span:nth-child(2n) {
          color: #f56c6c;
          background: #f8f8f8;
          padding: 0 5px;
          margin-right: 30px;
        }
      }

      .card_content {
        margin-left: 20px;
        padding: 3px 10px;
        color: #f56c6c;
        background: #f8f8f8;
      }
    }
  }

  .import-info {
    margin-top: 10px;
    color: #409EFF;
  }
</style>



